<template>
  <div class="casino container">
    <div class="row">
      <h1>欢迎来到token制造中心</h1>
      <br/>
      <h3>填写token相关信息</h3>
      <br/> 
      <form class="form-horizontal" role="form">

        <div class="col-sm-4 height margin"></div>
        <div class="form-group col-sm-4 height margin">
          <label for="firstname"  class="col-sm-4 control-label">代币数量</label>
          <div class="col-sm-8">
            <input type="text" class="form-control" v-model="initialAmount" placeholder="例如: 21000000" required="required">
          </div>
        </div>
        <div class="col-sm-4 height margin"></div>

        <div class="col-sm-4 height margin"></div>
        <div class="form-group col-sm-4 height">
          <label for="firstname" class="col-sm-4 control-label">代币名称</label>
          <div class="col-sm-8">
            <input type="text" class="form-control"  v-model="name" placeholder='例如: "SiaCoin"' required="required">
          </div>
        </div>
        <div class="col-sm-4 height margin"></div>

        <div class="col-sm-4 height margin"></div>
        <div class="form-group col-sm-4 height">
          <label for="firstname"  class="col-sm-4 control-label">代币符号</label>
          <div class="col-sm-8">
            <input type="text" class="form-control" v-model="tokenSymbo" placeholder='例如: "SC"' required="required">
          </div>
        </div>
        <div class="col-sm-4 height margin">
        </div>
   
         <div class="col-sm-4 height margin"></div>
        <div class="form-group col-sm-4 height margin">
          <label for="firstname"  class="col-sm-4 control-label">钱包地址</label>
          <div class="col-sm-8">
            <input type="text" class="form-control" v-model="owneraddress" placeholder="例如: 0x14acXXXXXX">
          </div>
        </div>
        <div class="col-sm-4 height margin">
          <button type="button" class="btn btn-success" @click="depolyContract">生成合约</button>
        </div>

        <img v-if="pending" id="loader" src="https://loading.io/spinners/double-ring/lg.double-ring-spinner.gif">
        <div class="col-sm-12 height margin">
         <h4>{{msg}}</h4>
       </div>
      </form>
    </div> 
  </div>
</template>

<script>
import Web3 from 'web3'
import {BYTECODE, ABI} from './../util/constants/casinoContract'
import {mapState} from 'vuex'
export default {
  name: 'casino',
  data () {
    return {
      initialAmount: null,
      name: '',
      tokenSymbo:'',
      owneraddress: '',
      pending: false,
      sucess: false,
      msg: '',
      casinoContractInstance: null,
      oldbalance: 0
    }
  },
  computed: {
    ...mapState({currentChange: state => state.web3.change})
  },
  watch: {
    currentChange: function(){
      if(this.currentChange == 1){
        this.transfer()
      }
      if(this.currentChange == 2){
        this.pending = false
        this.msg = "代币已发送到指定钱包，请注意查收"
      }
    }
  },
  methods: {
    clickToken () {
      this.msg = "等待支付，支付完成后开始发行代币"
      this.pending = false
      this.casinoContractInstance().makeCoin(this.initialAmount,this.name,0,this.tokenSymbo, {from: this.$store.state.web3.coinbase},(err, result) => {
        if (err) {
          this.pending = false
          this.msg = "代币发行失败"
        } else {
          this.pending = true
          this.msg = "支付完成，正在发行代币，请等待..."
          this.$store.commit('isReadyMakeCoin')
        }
      } 
     )
    },
    depolyContract () {
      const web3 = new Web3(window.web3.currentProvider)
      const coinContract = web3.eth.contract(ABI)
      if(this.initialAmount == null || this.name == '' || this.tokenSymbo == '' || this.owneraddress == '' ) {
        this.msg = "请完整填写相关信息"
        return
      }
      this.pending = true
      this.msg = "等待支付，支付成功后开始部署合约"
      const self = this
      coinContract.new({
        from: web3.eth.accounts[0],
        data: "",
        gas: "3000000",
        gasPrice: 0
      },function(err,contract){
           if (!err) {
            self.msg = "支付成功，开始部署合约。。。"
            if (contract.address !== undefined) {
               self.msg = "合约部署完成"
               console.log('合约部署地址: ' + contract.address + ' 交易哈希: ' + contract.transactionHash)
               self.casinoContractInstance = () => coinContract.at(contract.address)
               self.clickToken()
              }else{
              console.log('合约部署失败，交易哈希： ' + contract.transactionHash)
            }
          }else{console.log("发生错误："+err)}
        }
      )  
    },  
    transfer () {
         this.pending = false
         this.msg = "代币发行成功，请继续支付，支付完成后开始转移代币"
         this.casinoContractInstance().transfer(this.owneraddress,this.initialAmount,{from: this.$store.state.web3.coinbase},(err, result) => {
         if (err) {
           console.log("代币转移失败:"+err)
          } else {
          this.pending = false
          this.msg = "开始转移代币..."
          this.$store.commit('isReadyMakeCoin')
         }
       })
    },
    balanceOf () {
      this.$store.state.contractInstance().balanceOf(this.owneraddress,{from: this.$store.state.web3.coinbase},(err, result) => {
        if (err) {
          console.log(err)
        } else {
          console.log(result)
        }
      } 
      )
    }
  }
}
</script>

<style scoped>

.casino {
     margin-top: 50px;
     text-align:center;
}
.height {
  height: 35px;
}
.margin {
  margin-bottom: 15px;
}
#loader {
  width:150px;
}
ul {
    margin: 25px;
    list-style-type: none;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-column-gap:25px;
    grid-row-gap:25px;
}
li{
    padding: 20px;
    margin-right: 5px;
    border-radius: 50%;
    cursor: pointer;
    background-color:#fff;
    border: -2px solid #bf0d9b;
    color: #bf0d9b;
    box-shadow:3px 5px #bf0d9b;
}
li:hover{
    background-color:#bf0d9b;
    color:white;
    box-shadow:0px 0px #bf0d9b;
}
li:active{
    opacity: 0.7;
}
*{
   color: #444444;
}
#has-won {
  color: green;
}
#has-lost {
  color:red;
}
</style>


